.section_container {
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 12vw;
}

#meImg {
    object-fit: cover;
    width: 25vw;
    height: 25vw;
    border-radius: 50px;
}

.text_template1 {
    padding: 2.5vw;
    border-radius: 20px;
    background-color: #292c39;
}

.text_template1::after {
    content: "";
    position: absolute;
    border-radius: 20px;
    bottom: 20vh;
    right: 10vw;
    width: 40vw;
    height: 50vh;
    background-color: #1d202f;
    z-index: -1;
    -webkit-box-shadow: 10px 10px 15px 10px #1d202f;
    box-shadow: 10px 10px 15px 10px #1d202f;
    animation: A_breathe 2s infinite;
}

.text_template1 h1 {
    font-size: 2.5vw;
    margin-bottom: 10px;
}

.text_template1 p {
    font-size: 1.2vw;
    line-height: 1.6vw;
}

#meImg_container {
    margin-right: 125px;
}

.typewriter h1 {
    color: #fff;
    overflow: hidden;
    border-right: 0.15em solid rgb(136, 136, 241);
    white-space: nowrap;
    letter-spacing: 0.3rem;
    animation: typing 3s steps(30, end), blink-caret 0.5s step-end infinite;
    width: fit-content;
}

/* The typing effect */
@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: 100%;
    }
}

/* The typewriter cursor effect */
@keyframes blink-caret {
    from,
    to {
        border-color: transparent;
    }
    50% {
        border-color: rgb(136, 136, 241);
    }
}

#contents_list p {
    font-size: 1.2vw;
    line-height: 40px;
    color: #d8d8d8;
    flex: 1;
}

#contents_list div {
    font-size: 1.8vw;
    display: flex;
    align-items: center;
}

#contents_list a {
    display: flex;
    flex-direction: row;
}

#contents_list a:hover {
    -webkit-box-shadow: 0px 0px 10px 0px rgba(60, 103, 227, 0.9);
    -moz-box-shadow: 0px 0px 10px 0px rgba(60, 103, 227, 0.9);
    box-shadow: 0px 0px 10px 0px rgba(60, 103, 227, 0.9);
    background-color: rgba(60, 103, 227, 0.4);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
    transition: all 0.3s ease;
    border-radius: 5px;
    padding: 0 10px;
    font-weight: 600;
}

#skateVideo video {
    width: 25vw;
    border-radius: 50px;
    display: block;
}

#artImg img {
    width: 25vw;
    border-radius: 50px;
    display: block;
}

#artImg {
    margin-right: 150px;
}

#skateVideo {
    margin-left: 150px;
}

.box_title {
    letter-spacing: 2px;
    font-size: 40px;
    margin-bottom: 10px;
}

.box_desc {
    font-size: 1.2vw;
    line-height: 1.6vw;
}

.q_link {
    line-height: 200%;
    font-size: 1.1vw;
    text-decoration: underline rgb(136, 136, 241);
    text-underline-offset: 0.5vh;
    text-decoration-thickness: 3px;
    border-radius: 5px;
    transition: all 0.2s ease-in;
    padding: 5px;
}

.q_link:hover {
    background-color: rgba(60, 103, 227, 0.4);
    text-decoration: underline rgb(158, 200, 242);
}

#PERImg_container {
    display: flex;
    flex-direction: row;
    overflow: hidden;
    border-radius: 20px;
}

#PERImg_container img {
    flex: 1;
    height: 16vw;
    width: 25vw;
    object-fit: cover;
}

#PERImg_text {
    width: 70vw;
    background-color: #292c39;
    padding: 2vw;
    border-radius: 20px;
    margin-top: 2vw;
}

#PERImg_text h1 {
    letter-spacing: 2px;
    font-size: 2vw;
}

#PERImg_text h2 {
    font-size: 1.4vw;
    margin-bottom: 1vw;
    color: rgb(179, 179, 179);
}

#PERImg_text p {
    font-size: 1.2vw;
    line-height: 1.6vw;
}

#PERImg_text::before {
    content: "";
    position: absolute;
    transform: translate(-10vw, -10vw);
    z-index: -2;
    background-color: rgb(89, 84, 157);
    width: 300px;
    height: 300px;
    background: linear-gradient(45deg, #9a73b4 0%, #4751c0 100%);
    animation: morph 3s ease-in-out infinite;
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    transition: all 1s ease-in-out;
}

#PERImg_text::after {
    content: "";
    position: absolute;
    right: 0;
    transform: translate(-7vw, -7vw);
    z-index: -2;
    background-color: rgb(118, 123, 155);
    width: 15vw;
    height: 15vw;
    border-radius: 50%;
    background: linear-gradient(45deg, #88d5bf 0%, #5d6bf8 100%);
    animation: morph 3s ease-in-out infinite;
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
    transition: all 1s ease-in-out;
}

@media screen and (max-width: 1200px) {
    .section_container {
        min-height: 100vh;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-direction: column;
        padding: 0 8vw;
    }

    #meImg {
        object-fit: cover;
        width: 70vw;
        height: 70vw;
        border-radius: 20px;
    }

    .text_template1 {
        padding: 6vw;
        border-radius: 20px;
        width: 70vw;
        text-wrap: wrap;
    }

    .text_template1::after {
        display: none;
    }

    .text_template1 h1 {
        font-size: 4.8vw;
        margin-bottom: 10px;
    }

    .typewriter h1 {
        color: #fff;
        overflow: hidden;
        border-right: 0.15em solid rgb(136, 136, 241);
        white-space: nowrap;
        letter-spacing: 0.1rem;
        animation: typing 3s steps(30, end), blink-caret 0.5s step-end infinite;
        width: fit-content;
    }

    .text_template1 p {
        font-size: 3.4vw;
        line-height: 5vw;
    }

    #meImg_container {
        margin-right: 0;
        margin-bottom: 10vw;
    }

    #contents_list p {
        font-size: 3.6vw;
        color: #d8d8d8;
        flex: 1;
        display: flex;
        align-items: center;
    }

    #contents_list div {
        font-size: 6vw;
        display: flex;
        align-items: center;
    }

    #contents_list a:hover {
        -webkit-box-shadow: 0px 0px 10px 0px rgba(60, 103, 227, 0.9);
        -moz-box-shadow: 0px 0px 10px 0px rgba(60, 103, 227, 0.9);
        box-shadow: 0px 0px 10px 0px rgba(60, 103, 227, 0.9);
        background-color: rgba(60, 103, 227, 0.4);
        backdrop-filter: blur(10px);
        -webkit-backdrop-filter: blur(10px);
        transition: all 0.3s ease;
        border-radius: 5px;
        padding: 0 3vw;
        font-weight: 600;
    }

    #skateVideo {
        margin-left: 0;
        margin-top: 10vw;
    }

    #skateVideo video {
        width: 70vw;
        border-radius: 20px;
    }

    #artImg img {
        width: 70vw;
        border-radius: 20px;
    }

    #artImg {
        margin-right: 0;
        margin-bottom: 10vw;
    }

    .q_link {
        font-size: 3vw;
        line-height: 180%;
        padding: 0;
    }

    #PERImg_container {
        display: flex;
        flex-direction: row;
        overflow: hidden;
        border-radius: 20px;
    }

    #PERImg_container img {
        flex: 1;
        height: 30vw;
        width: 30vw;
        object-fit: cover;
    }

    #PERImg_text {
        width: 80vw;
        background-color: #292c39;
        padding: 6vw;
        border-radius: 20px;
        margin-top: 10vw;
    }

    #PERImg_text h1 {
        letter-spacing: 1vw;
        font-size: 6vw;
    }

    #PERImg_text h2 {
        font-size: 4.5vw;
        margin-bottom: 3vw;
        color: rgb(179, 179, 179);
    }

    #PERImg_text p {
        font-size: 3.4vw;
        line-height: 5vw;
    }

    #PERImg_text::before {
        content: "";
        position: absolute;
        transform: translate(-10vw, -60vw);
        z-index: -2;
        background-color: rgb(89, 84, 157);
        width: 50vw;
        height: 50vw;
        background: linear-gradient(45deg, #9a73b4 0%, #4751c0 100%);
        animation: morph 3s ease-in-out infinite;
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
        transition: all 1s ease-in-out;
    }

    #PERImg_text::after {
        content: "";
        position: absolute;
        right: 0;
        transform: translate(-1vw, -10vw);
        z-index: -2;
        background-color: rgb(118, 123, 155);
        width: 40vw;
        height: 40vw;
        border-radius: 50%;
        background: linear-gradient(45deg, #88d5bf 0%, #5d6bf8 100%);
        animation: morph 3s ease-in-out infinite;
        border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
        transition: all 1s ease-in-out;
    }
}
